.icon-left{
position: absolute;
top: 10px;
left: 10px;
}


.icon-right{
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display{
    display: grid;
    place-items: center;
    height: 100vh;
}

.season-display.winter i {
    color: blue;
}

.season-display.summer i{
    color: orangered;
}

.winter{
    background-color: aquamarine;
}

.summer{
    background-color:orange;
}